.App {
  position: absolute;
  left: 50%;
  margin-left: -25%;  
}

.Game {
  clear: both;
  padding-top: 5px;
}

.Timer {
  color: gray;
  float: left;
}

.Assistant {
  right: 0;
  display: inline-block;
  float: right;
}

.Overlay {
  position: absolute;
  left: 0;
  top: 0;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100%;
  font-size: 1em;
  text-align: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.Row {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 0 1 auto;
      flex: 0 1 auto;
  -ms-flex-direction: row;
      flex-direction: row;
  border-top: 0px solid transparent;
}

.Field {
  display: inline-block;
  outline: 1px solid gray;
  background: white;
  text-align: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 25px;
  height: 25px;
  line-height: 25px;
  border: 1px solid transparent;
}

.SixthRow .Field {
  border-top: 1px solid gray;
}

.SixthField {
  border-left: 1px solid gray;
}

.ValueField {
  color: lightcyan;
}

.Hint.UserComplete,
.EmptyHint.UserComplete {
  opacity: 0.3;
}

.Hint {
  background: gray;
}

.EmptyHint {
  background: white;
}


